<template>
  <div v-show="visible">
    <div class="modal fade in" id="myModal" style="display: block; padding-right: 21px;">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" aria-hidden="true" @click="close"></button>
                    <h4 class="modal-title" id="myModalLabel"> > </h4>
                </div>
                <div class="modal-footer">
                   <button type="button" class="btn btn-default PU" data-dismiss="modal"><span class="glyphicon glyphicon-edit" style="padding-right:10px;"></span></button>
                   <button type="button" class="btn btn-default PU" data-dismiss="modal"><span class="fa fa-save" style="padding-right:10px;"></span></button>
                   <button type="button" class="btn btn-default PU" @click="handleAdd"><span class="fa fa-upload" style="padding-right:10px;"></span></button>
                   <button style="display:none;" type="button" class="btn btn-default PU" data-dismiss="modal"><span class="fa fa-check-circle-o" style="padding-right:10px;"></span></button>
                   <button style="display:none;" type="button" class="btn btn-default PU" data-dismiss="modal"><span class="glyphicon glyphicon-minus-sign" style="padding-right:10px;"></span></button>
                </div>
                <form id="register-form" role="form" class="form-horizontal" method="get">
                <div class="modal-body" id="validatorDiv">
                  <!--first start-->
                        <div style="width:450px; float:left;">
                          <div class="form-group has-feedback cusGroup">
                              <label class="col-sm-4 control-label cusConLabel">:</label>
                              <div class="col-sm-8">
                                <input type="text" id="Code" class="form-control" value="" placeholder="" required="" data-fv-notempty-message="" data-fv-field="name"  v-model="Code" disabled/>
                            </div>
                          </div>

                          <div class="form-group has-feedback cusGroup">
                            <label class="col-sm-4 control-label cusConLabel"><span class="star">*</span>:</label>
                            <div class="col-sm-8">
                              <!--<input type="text" class="form-control" name="cusType" value="" placeholder="" required="" data-fv-notempty-message="" data-fv-field="machineNo">-->
                              <select class="form-control" id="CustomerAuditType" v-model="CustomerAuditType" type="select" name="CustomerAuditType" lable="">
                                <option></option>
                                <option value="10"></option>
                                <option value="70"></option>
                                <option value="80"></option>
                              </select>
                            </div>
                          </div>
                          <div class="form-group cusGroup">
                            <label class="col-sm-4 control-label cusConLabel"><span class="star">*</span>:</label>
                            <div class="col-sm-8 form-group input-group" style="padding-left:14px;width:306px;">
                                <input id="ParentId" :disabled="parentIddisabled" class="form-control" name="cusTopCompany" value="" placeholder="">
                                <span class="input-group-btn" v-show="treeSearch" style="line-height:34px;">
                                    <button class="btn btn-default" type="button"><i class="fa fa-search"></i>
                                    </button>
                                </span>
                            </div>
                            <div class="col-sm-8" style="display:none;">
                              <input id="ParentId" type="text" class="form-control" name="cusTopCompany" value="" placeholder="">
                            </div>
                          </div>
                          <div class="form-group cusGroup">
                            <label  class="col-sm-4 control-label cusConLabel"><span class="star">*</span>:</label>
                            <div class="col-sm-8">
                              <!--<input type="text" class="form-control" name="cusBegin" value="" placeholder="">-->

                                <el-date-picker
                                  v-model="StatrMonth"
                                  type="date"
                                  format="MM-dd"
                                  placeholder=""
                                  :picker-options="pickerOptions0">
                                </el-date-picker>

                            </div>

                          </div>
                          <div class="form-group cusGroup">
                            <label class="col-sm-4 control-label cusConLabel"><span class="star">*</span>:</label>
                            <div class="col-sm-8">
                              <input type="text" id="OrgCode" v-model="OrgCode" class="form-control" name="cusCode" value="" placeholder="">
                            </div>
                          </div>
                          <div class="form-group cusGroup">
                            <label class="col-sm-4 control-label cusConLabel"><span class="star">*</span>:</label>
                            <div class="col-sm-8">
                              <!--<input type="text" class="form-control" name="cusMoneyType" value="" placeholder="">-->
                              <select class="form-control" v-model="Currency" id="Currency" type="select" name="Currency" controltype="Combobox" lable="">
                                <option></option>
                                <option value="10"></option>
                                <option value="20"></option>
                                <option value="30"></option>
                                <option value="40"></option>
                                <option value="50"></option>
                              </select>
                            </div>
                          </div>
                        </div>
                        <!--first end-->
                        <!--second start-->
                        <div style="width:450px; float:left;">
                          <div style="display:block;" class="form-group cusGroup">
                              <label class="col-sm-4 control-label cusConLabel" for="Name"><span class="star">*</span>:</label>
                              <div class="col-sm-8">
                                <input type="text" class="form-control" id="Name" v-model="Name" name="Name" value="" placeholder="" />
                              </div>
                          </div>


                          <div class="form-group has-feedback cusGroup">
                            <label class="col-sm-4 control-label cusConLabel"><span class="star">*</span>:</label>
                            <div class="col-sm-8">
                              <input type="text" class="form-control" id="Level" value="" disabled v-model="companyLevel">
                            </div>
                          </div>
                          <div class="form-group cusGroup">
                            <label class="col-sm-4 control-label cusConLabel"><span class="star">*</span>:</label>
                            <div class="col-sm-8">
                              <input type="text" id="TopCustomerId" class="form-control" name="cusTopCompany" value="" placeholder="">
                            </div>
                          </div>
                          <div class="form-group cusGroup">
                            <label  class="col-sm-4 control-label cusConLabel"><span class="star">*</span>:</label>
                            <div class="col-sm-8">
                              <!--<input type="text" class="form-control" name="cusBegin" value="" placeholder="">-->

                              <el-date-picker
                                v-model="EndMonth"
                                type="date"
                                disabled
                                placeholder=""
                                format='MM-dd'
                                :picker-options="pickerOptions0">
                              </el-date-picker>

                            </div>
                          </div>
                          <div class="form-group cusGroup">
                            <label class="col-sm-4 control-label cusConLabel"><span class="star">*</span>:</label>
                            <div class="col-sm-8">
                              <!--<input type="text" class="form-control" name="cusCode" value="" placeholder="">-->
                              <select class="form-control" v-model="Industry" id="Industry" lable="">
                                <option></option>
                                <option value="411"></option>
                                <option value="433"></option><option value="449"></option><option value="646"></option><option value="656"></option><option value="663"></option><option value="696"></option><option value="710"></option><option value="731"></option><option value="741"></option><option value="762"></option><option value="768"></option><option value="782"></option><option value="806"></option><option value="818"></option><option value="833"></option><option value="840"></option><option value="854"></option><option value="882"></option><option value="906"></option><option value="910"></option></select>
                            </div>
                          </div>
                        </div>
                        <!--second end-->
                        <div style="clear:both"></div>

                </div>
              </form>
            </div>
            <!-- /.modal-content -->
        </div>
        <!-- /.modal-dialog -->

    </div>
    <div class="modal-backdrop fade in"></div>
  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'

export default {
  data(){
    return {
      pickerOptions0: {
        disabledDate(time) {
          //return time.getTime() < Date.now() - 8.64e7;
        }
      },
       StatrMonth: '',//
       CustomerAuditType:'10',
       Name:'',//
       OrgCode:'',//
       Industry:'',//
       Currency:'',//
       treeSearch:false,//
       parentIddisabled:true,//id
    }
  },
  computed: {
    visible: function () {
      return this.$store.state.customer.CusAddModalVisible
    },
    Code: function(){
      return this.$store.state.customer.Code
    },
    companyLevel: function(){
      var level = ''
      if(this.CustomerAuditType == 10){
        this.treeSearch = false
        this.parentIddisabled=true
        level=1
      }
      else if(this.CustomerAuditType == 70){
        this.treeSearch=true
        this.parentIddisabled=false
        level=2
      }
      else if(this.CustomerAuditType == 80){
        this.treeSearch=true
        this.parentIddisabled=false
       level=3
      }
      return level
    },
    EndMonth: function(){//
      if(this.StatrMonth){
        const date = new Date();
        return date.setTime(this.StatrMonth- 3600 * 1000 * 24);
      }else {
        return ''
      }
    },
  },
  methods: {
    // 
    close: function () {
      this.$store.dispatch('showAddModal', {isShow: false})
    },
    handleAdd: function () {
      console.log(this.Name)
      if(this.Name==''){//
        this.$store.commit('MsgModalMut',{isShow:true,msg:""})
        $("#Name").addClass("has-error")
        $("#Name").focus();
        return
      }else {
        $("#Name").removeClass("has-error")
      }
      if(this.CustomerAuditType == ''){//
        this.$store.commit('MsgModalMut',{isShow:true,msg:""})
        $("#CustomerAuditType").addClass("has-error")
        $("#CustomerAuditType").focus();
        return
      }else {
          $("#CustomerAuditType").removeClass("has-error")
      }
      if(this.StatrMonth == ''){//
        this.$store.commit('MsgModalMut',{isShow:true,msg:""})
        $("#StatrMonth").addClass("has-error")
        $("#StatrMonth").focus();
        return
      }else {
          $("#StatrMonth").removeClass("has-error")
      }
      if(this.OrgCode == '' || this.OrgCode.length != 18){//
        this.$store.commit('MsgModalMut',{isShow:true,msg:""})
        $("#OrgCode").addClass("has-error")
        $("#OrgCode").focus();
        return
      }else {
          $("#OrgCode").removeClass("has-error")
      }
      if(this.Industry == ''){//
        this.$store.commit('MsgModalMut',{isShow:true,msg:""})
        $("#Industry").addClass("has-error")
        $("#Industry").focus();
        return
      }else {
          $("#Industry").removeClass("has-error")
      }
      if(this.Currency == ''){//
        this.$store.commit('MsgModalMut',{isShow:true,msg:""})
        $("#Currency").addClass("has-error")
        $("#Currency").focus();
        return
      }else {
          $("#Currency").removeClass("has-error")
      }


      this.$store.dispatch('addClient',{
        Timestamp:'',
        Code: $('#Code').val(),
        Name: $('#Name').val(),
        CustomerAuditType: $('#CustomerAuditType').val(),
        Level: $('#Level').val(),
        ParentId: $('#ParentId').val(),
        TopCustomerId: $('#TopCustomerId').val(),
        StatrMonth: moment(this.StatrMonth).format("YYYY-MM-DD").substring(5,7),
        EndMonth: moment(this.EndMonth).format("YYYY-MM-DD").substring(5,7),
        OrgCode: $('#OrgCode').val(),
        Industry: $('#Industry').val(),
        Currency: $('#Currency').val(),
      })
    }
  },
  beforeCreate() {
    console.log('beforCreate')
  },
  created() {
    console.log('created')
  },
  beforeMount() {
    console.log('beforeMount');
  },
  mounted() {
    console.log('addCode')
/*
    //mounted 
    $("#register-form").validate({//
		    errorElement : 'span',
		    errorClass : 'help-block',
		rules : {
        Name : "required",
		},
		messages : {
      Name : "",
		},

		errorPlacement : function(error, element) {
			element.next().remove();
			element.after('<span class="glyphicon glyphicon-remove form-control-feedback" aria-hidden="true"></span>');
			element.closest('.form-group').append(error);
		},
		highlight : function(element) {
			$(element).closest('.form-group').addClass('has-error has-feedback');
		},
		success : function(label) {
			var el=label.closest('.form-group').find("input");
			el.next().remove();
			el.after('<span class="glyphicon glyphicon-ok form-control-feedback" aria-hidden="true"></span>');
			label.closest('.form-group').removeClass('has-error').addClass("has-feedback has-success");
			label.remove();
		},
		submitHandler: function(form) {
			alert("submitted!");
		}

	})//
*/
  //datepick
  //$("#upAddDateAddInput").datepicker();
  //var format = 'YYYY-MM-DD';
	//$('#upAddDateAddInput').datetimepicker({
  //              format: 'YYYY-MM-DD',
  //              showTodayButton:true,
  //          });
  //$('#upAddDateAddInput').datetimepicker({
  //   viewMode: 'years'
   //});
  //datepick
    //mounted 
  }
}
</script>

<style scoped lang="scss">
/****/
#addModal {margin-top: 200px; }
.modal-dialog {width: 960px;}
.modal-content{border-radius: 0px;}
.modal-header{background-color: #1dbba4; color:#fff;font-family:"Microsoft YaHei",Arial,Helvetica,sans-serif,"";}
.modal-header button{color: red;font-size: 24px;}
.modal-title{font-weight: normal; }
.cusGroup{line-height: 36px;}
.cusConLabel{text-align: right;font-weight: 400;}
.modal-footer{text-align: left;border-bottom: 1px solid #e5e5e5;}
.el-date-editor.el-input{width:290px;}
.star{color:red;padding-right: 4px;}
.form-horizontal .control-label{padding-top: 0px;}
.PU {color: #1dbba4;background-color: #fff;border-color:#1dbba4;font-size: 16px;}
.PU:hover{color: #fff;background-color: #1dbba4;border-color:#1dbba4;}
.has-error {border-color: #a94442;-webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075);box-shadow: inset 0 1px 1px rgba(0,0,0,.075);}
</style>



